.box-form {
    width: 544px;
    border: 1px solid var(--fourth-color);
    border-radius: 16px;
    padding-top: 64px;
    padding-left: 64px;
    padding-right: 64px;
    background-color: var(--seventh-color);
}

h1 {
    font-size: 48px;
    text-align: left;
    margin-bottom: 32px;
    text-align: left;
}

p {
    font-size: 20px;
    margin-bottom: 64px;
    letter-spacing: 0px;
}
form {
    display: grid;
    grid-template-columns: 1fr;
}

input {
    padding-left: 60px;
    margin-bottom: 16px;
}

#email::placeholder {
    color: #6d7a80;
    letter-spacing: 0px;
}

input,
.form-btn {
    width: 100%;
    font-size: 20px;
    padding-top: 20px;
    padding-bottom: 17px;
    border: 1px solid var(--fourth-color);
    border-radius: 8px;
}

.form-btn {
    color: var(--third-color);
    background-color: var(--second-color);
    margin-bottom: 153px;
    text-transform: capitalize;
}

i {
    position: absolute;
    padding-top: 24.33px;
    padding-left: 20px;
}

form {
    span {
        font-size: 16px;
        margin-bottom: 63px;
        color: #6d7a80;
    }
}

@media (max-width: 375px) {
    .box-form {
        width: 100%;
    }
}
